<template>
  <div>
    <app-header></app-header>
    <div class="vished-detail-wrap w">
      <h2>{{contacts.locationInfo.name}}</h2>
      <h5>{{contacts.locationInfo.city}} {{contacts.locationInfo.state}}</h5>
      <ul class="vished-ul">
        <li class="vished-ul-li" v-for="(item,index) in contacts.contact" :key="index">
          <div class="vished-ul-li-title">
            <span>{{item.preciseLocation}}</span>
            <span class="vished-ul-li-number">1</span>
          </div>
          <p>{{dateFormat(item.time)}}</p>
        </li>
      </ul>
    </div>
    <app-nav></app-nav>
  </div>
</template>

<script>
import {getContacts, dateFormat} from '@/api/firebase'
export default {
  components: {
    'app-nav': () => import('@/components/nav'),
    'app-header': () => import('@/components/header')
  },
  created () {
    getContacts().then(res => {
      this.contacts = res[this.$route.params.id]
    })
  },
  data () {
    return {
      contacts: []
    }
  },
  methods: {
    dateFormat (date) {
      return dateFormat(Date(date), true)
    }
  }
}
</script>

<style scoped>
.vished-ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.vished-ul-li {
  border-bottom: 1px solid #e5e5e5;
  margin: 10px 0;
}

.vished-ul-li .vished-ul-li-title {
  font-weight: bold;
  display: flex;
  justify-content: space-between;
}

.vished-ul-li-number {
  font-size: 25px;
}
</style>



